<template lang="pug">
    section#intro
        .bg
            img.bg__img.cover-img.wow.fadeIn.faster(:src="baseUrl + intro.bg")
            .bg__show.wow.slow.slideOutLeft
        .container
            .content
                h1.title.wow.fadeInUp(data-wow-delay="1s") {{ intro.title }}
                p.desc.wow.fadeInUp(data-wow-delay="1.5s") {{ intro.desc }}
                button.btn.wow.zoomIn(data-wow-delay="2s") {{ intro.btnTitle }}
            img.image.cover-img.wow.zoomIn(:src="baseUrl + intro.image" data-wow-delay="1s")
</template>

<script>
    import json from "@/store/intro"

    export default {
        name: "Intro",
        data() {
            return {
                baseUrl: "../",
                intro: json
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/sass/style";

    #intro {
        width: 100%;
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        @include breakpoint-down($lg-max) {
            min-height: 568px;
        }
    }
    .container {
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        @include breakpoint-down($md-max) {
            flex-direction: column;
            justify-content: center;
        }
    }
    .content {
        flex: 1;
        margin-right: 50px;
        @include breakpoint-down($md-max) {
            margin-right: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
    .title {
        text-transform: uppercase;
        font-size: 3rem;
        margin-bottom: $gutter-sm;
        font-weight: 900;
        @include breakpoint-down($lg-max) {
            font-size: 2rem;
        }
        @include breakpoint-down($md-max) {
            font-size: 1.5rem;
            text-align: center;
        }
    }
    .desc {
        font-size: .75rem;
        margin-bottom: $gutter-md;
        line-height: 1.5;
        @include breakpoint-down($md-max) {
            text-align: center;
        }
    }
    .image {
        width: 50%;
        object-fit: contain;
        height: auto;
        padding: $gutter-md;
        @include breakpoint-down($md-max) {
            display: none;
        }
    }
    .btn {
        border-radius: 5px;
        background: $active-color;
        padding-left: $gutter-md;
        padding-right: $gutter-md;
        color: $main-color;
        text-transform: uppercase;
    }
</style>
