<template lang="pug">
    main#main
        img.bg-img.cover-img(:src="baseUrl + 'img/compliance.jpg'")
        section#order
            h2.title Оформление заявки
            p.desc Есть много вариантов Lorem Ipsum, но большинство из них имеет не всегда приемлемые модификации, например, юмористические вставки или слова, которые даже отдалённо не напоминают латынь.
            .order
                .order__wrapper
                    p.order__label Веб разработка*
                    Multiselect.order__select.order__order-select(:options="orderWebSelect" v-model="webSelect" track-by="name" label="name" placeholder="Выбрать" :searchable="false")
                .order__wrapper
                    p.order__label Таргетинг*
                    Multiselect.order__select.order__order-select(:close-on-select="false" :options="orderTargetSelect" v-model="targetSelect" track-by="name" label="name" :multiple="true" placeholder="Выбрать" :searchable="false")
                .order__wrapper
                    p.order__label Консультация
                    Multiselect.order__select.order__order-select(:options="consultationWebSelect" v-model="consultationSelect" track-by="name" label="name" placeholder="Выбрать" :searchable="false")
                .order__wrapper
                    p.order__label Введите ваше имя*
                    input.order__input.order__name(type="text" name="name" v-model="nameInput" placeholder="Ваше Имя")
                .order__wrapper
                    p.order__label Введите ваш телефон
                    input.order__input.order__phone(type="phone" name="phone" v-model="phoneInput" placeholder="Ваш телефон")
                .order__wrapper
                    p.order__label Введите вашу почту*
                    input.order__input.order__email(type="email" name="email" v-model="emailInput" placeholder="Ваша почта")
                .order__wrapper-text
                    p.order__label Ваш комментарий
                    textarea.order__textarea.order__comment(name="comment" v-model="commentInput" placeholder="Ваш комментарий" rows="5")
                button.order__btn.btn(@click="sendForm") Оформить заявку
        transition(name="successModal" enter-active-class="animated fadeIn" leave-active-class="animated fadeOut")
            Success(v-if="showSuccess")
            Empty(v-if="showEmpty")
            Error(v-if="showError")
</template>

<script>
    import axios from 'axios'

    import Success from "@/components/modals/Success"
    import Empty from "@/components/modals/Empty"
    import Error from "@/components/modals/Error"

    import Multiselect from 'vue-multiselect'

    export default {
        name: "Order",
        data() {
            return {
                baseUrl: "../",
                errors: [],
                showSuccess: false,
                showEmpty: false,
                showError: false,

                orderWebSelect: [
                    {
                        key: "empty",
                        name: "-"
                    },
                    {
                        key: "start",
                        name: "Start"
                    },
                    {
                        key: "standard",
                        name: "Standard"
                    },
                    {
                        key: "business",
                        name: "Business"
                    }
                ],
                consultationWebSelect: [
                    {
                        key: "yes",
                        name: "Нужна"
                    },
                    {
                        key: "no",
                        name: "Не нужна"
                    }
                ],
                orderTargetSelect: [
                    {
                        key: "empty",
                        name: "-"
                    },
                    {
                        key: "vk",
                        name: " Реклама Vk"
                    },
                    {
                        key: "fb",
                        name: " Реклама Facebook"
                    },
                    {
                        key: "inst",
                        name: " Реклама Instagram"
                    }
                ],
                webSelect: [],
                targetSelect: [],
                consultationSelect: [],
                nameInput: "",
                phoneInput: "",
                emailInput: "",
                commentInput: ""
            }
        },
        mounted() {
            this.$nextTick(function () {
                this.checkQuery();
            })
        },
        components: {
            Success,
            Empty,
            Error,
            Multiselect
        },
        methods: {
            checkQuery() {
                if (this.$route.query.page === "web") {
                    let $chosenArr = [this.$route.query];
                    this.webSelect = $chosenArr;
                } else if (this.$route.query.page === "consultation") {
                    let $chosenArr = [this.$route.query];
                    this.consultationSelect = $chosenArr;
                } else if (this.$route.query.page === "target") {
                    let $chosenArr = [this.$route.query];
                    this.targetSelect = $chosenArr;
                }
            },
            sendForm() {
                if (this.webSelect && this.targetSelect && this.nameInput && this.emailInput) {
                    this.errors = [];
                    axios.post('https://formcarry.com/s/X43JFmJWiYA', {
                            web: this.webSelect.name,
                            target: this.targetSelect.map(function(item) {
                                return item.name;
                            }),
                            name: this.nameInput,
                            phone: this.phoneInput,
                            email: this.emailInput,
                            comment: this.commentInput
                        },
                        {
                            headers: {"Accept": "application/json"}
                        })
                        .then(response => {
                            this.webSelect = [];
                            this.targetSelect = [];
                            this.consultationSelect = [];
                            this.nameInput = "";
                            this.phoneInput = "";
                            this.emailInput = "";
                            this.commentInput = "";
                            this.showSuccess = true;
                            // console.log(response.data);
                        })
                        .catch(error => {
                            this.showError = true;
                            // console.log(error);
                        })
                } else if (this.webSelect.length === 0) {
                    this.showEmpty = true;
                } else if (this.targetSelect.length === 0) {
                    this.showEmpty = true;
                } else if (!this.nameInput) {
                    this.showEmpty = true;
                } else if (!this.emailInput) {
                    this.showEmpty = true;
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../assets/sass/style";

    #main {
        width: 100%;
        padding-top: 50px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        justify-content: flex-end;
        position: relative;
    }
    #order {
        padding: $gutter-lg;
        width: 50%;
        margin-left: auto;
        @include breakpoint-down($lg-max) {
            width: 75%;
        }
        @include breakpoint-down(650px) {
            width: 100%;
            padding: $gutter-sm;
        }
    }
    .bg-img {
        top: 50px;
        left: 0;
        position: absolute;
        width: 50%;
        height: calc(100% - 50px);
        @include breakpoint-down($lg-max) {
            width: 25%;
        }
        @include breakpoint-down(650px) {
            display: none;
        }
    }
    .title {
        margin-bottom: $gutter-md;
        text-transform: uppercase;
        color: $active-color;
        @include breakpoint-down($sm-max) {
            font-size: 1.25rem;
        }
    }
    .desc {
        font-size: .75rem;
        margin-bottom: $gutter-md;
    }
    .order {
        display: flex;
        flex-direction: column;
        align-items: stretch;
        &__wrapper {
            display: flex;
            flex-direction: row;
            align-items: center;
            border-bottom: solid 1px $dark-25;
            padding: $gutter-sm 0;
            @include breakpoint-down($sm-max) {
                flex-direction: column;
                align-items: flex-start;
            }
        }
        &__wrapper-text {
            margin-top: $gutter-sm;
            display: flex;
            flex-direction: column;
            align-items: stretch;
            .order__label {
                margin-bottom: $gutter-sm;
            }
        }
        &__label {
            font-size: .75rem;
            font-weight: bold;
            text-transform: uppercase;
            @include breakpoint-down($sm-max) {
                margin-bottom: $gutter-sm;
            }
        }
        &__select {
            font-size: .75rem;
            width: 50%;
            font-family: Montserrat;
            margin-left: auto;
            @include breakpoint-down($sm-max) {
                margin-left: 0;
                width: 100%;
            }
        }
        &__textarea {
            font-size: 1rem;
            font-family: Montserrat;
            margin-bottom: $gutter-md;
            width: 100%;
            margin-left: auto;
            height: auto;
            padding: $gutter-sm / 2 15px;
            border-radius: 5px;
            border: solid 1px $dark-25;
            resize: vertical;
            min-height: 115px;
        }
        &__input {
            font-size: 1rem;
            color: $dark-75;
            padding: 0 15px;
            width: 50%;
            height: 40px;
            margin-left: auto;
            border-radius: 5px;
            border: solid 1px $dark-25;
            @include breakpoint-down($sm-max) {
                margin-left: 0;
                width: 100%;
            }
        }
        &__btn {
            width: 50%;
            margin-left: auto;
            border-radius: 5px;
            background: $active-color;
            color: $main-color;
            text-transform: uppercase;
            @include breakpoint-down($sm-max) {
                margin-left: 0;
                width: 100%;
            }
        }
    }
</style>
