<template lang="pug">
    section#works
        .bg
            img.bg__img.cover-img.wow.fadeIn.faster(:src="baseUrl + works.bg")
            .bg__show.wow.slow.slideOutLeft
        .container
            h2.title.wow.fadeInUp(data-wow-delay="1s") {{ works.title }}
            ul.works
                li.works__work(v-for="work in works.works" :ref="work.key" :key="work.key")
                    img.works__image.cover-img.wow.zoomIn(:src="baseUrl + work.image" data-wow-delay="1.25s")
                    .works__control
                        h3.works__name.wow.fadeInUp(data-wow-delay="1.25s") {{ work.name }}
                        p.works__desc.wow.fadeInUp(data-wow-delay="1.5s") {{ work.desc }}
                        router-link.works__link.wow.zoomIn(tag="a" :to="work.link" data-wow-delay="1.75s") {{ works.btnTitle }}
</template>

<script>
    import json from "@/store/works"

    export default {
        name: "Works",
        data() {
            return {
                baseUrl: "../",
                works: json
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/sass/style";

    #works {
        width: 100%;
        min-height: 100vh;
        position: relative;
        margin-top: 100px;
    }
    .title {
        text-transform: uppercase;
        margin-bottom: $gutter-sm;
        @include breakpoint-down($lg-max) {
            font-size: 1.5rem;
        }
        @include breakpoint-down($md-max) {
            font-size: 1.25rem;
            text-align: center;
        }
    }
    .works {
        padding-top: $gutter-lg;
        position: relative;
        height: 100%;
        &__work {
            width: 100%;
            display: flex;
            flex-direction: row;
            align-items: center;
            margin-bottom: $gutter-lg;
            @include breakpoint-down($md-max) {
                flex-direction: column;
            }
            &:nth-child(even) {
                .works__image {
                    order: 2;
                    margin-right: 0;
                    margin-left: $gutter-lg;
                    @include breakpoint-down($md-max) {
                        margin-left: 0;
                        order: initial;
                    }
                }
            }
        }
        &__image {
            width: 50%;
            transition: all .25s ease-in-out;
            border-radius: 5px;
            margin-right: $gutter-lg;
            @include breakpoint-down($md-max) {
                width: 100%;
                margin-right: 0;
            }
            &:hover {
                transform: scale(1.1, 1.1)
            }
        }
        &__control {
            flex: 1;
            padding: $gutter-md 0;
            display: flex;
            flex-direction: column;
            width: 100%;
            position: relative;
            align-items: flex-start;
        }
        &__name {
            margin-bottom: $gutter-md;
        }
        &__desc {
            font-size: .75rem;
            line-height: 1.5;
            margin-bottom: $gutter-md;
        }
        &__link {
            font-weight: bold;
            text-decoration: none;
            transition: all .25s ease-in-out;
            position: relative;
            &:hover {
                color: $active-color;
                &:after {
                    right: -$gutter-md * 1.5;
                    width: $gutter-md;
                    background: $active-color;
                }
            }
            &:after {
                transition: all .25s ease-in-out;
                position: absolute;
                top: 50%;
                transform: translateY(-50%);
                display: block;
                right: -$gutter-md;
                content: "";
                width: $gutter-sm;
                height: 2px;
                background: $second-main-color;
            }
        }
    }
</style>
