<template lang="pug">
    section#orders
        .bg
            img.bg__img.cover-img.wow.fadeIn.faster(:src="baseUrl + orders.bg")
            .bg__show.wow.slow.slideOutLeft
        .container
            .content
                h2.title.wow.fadeInUp(data-wow-delay="1s") {{ orders.title }}
                p.desc.wow.fadeInUp(data-wow-delay="1.5s") {{ orders.desc }}
            ul.orders
                li.orders__order.wow.zoomIn(data-wow-delay="1.5s")(v-for="order in orders.orders" :key="order.key")
                    img.orders__img.cover-img(:src="baseUrl + order.image")
                    .orders__content
                        h6.orders__name {{ order.name }}
                        router-link.orders__icon.fas.fa-expand(tag="i" :to="order.link")
</template>

<script>
    import json from "@/store/orders"

    export default {
        name: "Orders",
        data() {
            return {
                baseUrl: "../",
                orders: json
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/sass/style";

    #orders {
        width: 100%;
        position: relative;
    }
    .title {
        text-transform: uppercase;
        margin-bottom: $gutter-sm;
        @include breakpoint-down($lg-max) {
            font-size: 1.5rem;
        }
        @include breakpoint-down($md-max) {
            font-size: 1.25rem;
            text-align: center;
        }
    }
    .desc {
        font-size: .75rem;
        margin-bottom: $gutter-md;
        line-height: 1.5;
        @include breakpoint-down($md-max) {
            text-align: center;
        }
    }
    .container {
        display: flex;
        flex-direction: row;
        align-items: center;
        flex-wrap: wrap;
    }
    .content {
        flex: 1;
        @include breakpoint-down($lg-max) {
            width: 100%;
            flex: none;
            margin-bottom: $gutter-md;
        }
    }
    .orders {
        margin-left: $gutter-lg;
        width: 50%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: space-between;
        align-items: center;
        @include breakpoint-down($lg-max) {
            width: 100%;
            margin-left: 0;
        }
        &__order {
            width: calc((100% / 2) - #{$gutter-sm});
            overflow: hidden;
            margin-bottom: $gutter-md;
            background: $main-color;
            border-radius: 5px;
            box-shadow: 0 0 5px $dark-25;
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: center;
            text-decoration: none;
            position: relative;
            height: 200px;
            @include breakpoint-down($sm-max) {
                width: 100%;
            }
            &:hover {
                .orders__content {
                    opacity: 1;
                }
            }
        }
        &__img {
            position: absolute;
            top: 0;
        }
        &__content {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            position: relative;
            opacity: 0;
            transition: all .5s ease-in-out;
            width: 100%;
            height: 100%;
            flex: 1;
            padding: $gutter-md;
            background: $dark-5;
        }
        &__name {
            color: $main-color;
            margin-bottom: $gutter-sm;
            text-transform: uppercase;
        }
        &__icon {
            cursor: pointer;
            color: $main-color;
            font-size: 1.25rem;
        }
    }
</style>
