                p.success__desc Что-то пошло не так. Повторите отправку еще раз
            button.success__btn.btn(@click="updateModal") Закрыть
</template>

<script>
    export default {
        name: "Error",
        data() {
            return {
                baseUrl: "../"
            }
        },
        methods: {
            updateModal() {
                this.$parent.showError = false;
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/sass/style";

    #success {
        position: fixed;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background: $dark-25;
        z-index: 99;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
    }
    .success {
        width: 500px;
        box-shadow: 0 0 25px $dark-25;
        border-radius: 5px;
        background: $full-white;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        align-items: center;
        @include breakpoint-down($md-max) {
            width: 300px;
        }
        &__content {
            width: 100%;
            padding: $gutter-md;
            background: $red;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        &__icon {
            color: $main-color;
            font-size: 5rem;
            margin-bottom: $gutter-md;
        }
        &__title {
            text-align: center;
