<template lang="pug">
    section#about
        .bg
            img.bg__img.cover-img.wow.fadeIn.faster(:src="baseUrl + about.bg")
            .bg__show.wow.slow.slideOutRight
        .container
            img.image.cover-img.wow.zoomIn(:src="baseUrl + about.image" data-wow-delay="1s")
            .content
                h2.title.wow.fadeInUp(data-wow-delay="1s") {{ about.title }}
                p.desc.wow.fadeInUp(data-wow-delay="1.5s") {{ about.text }}
                router-link.btn.wow.zoomIn(tag="button" :to="{ path: '/order', query: { page: 'consultation', key: 'yes', name: 'Нужна' } }" data-wow-delay="2s") {{ about.btnTitle }}
</template>

<script>
    import json from "@/store/about"

    export default {
        name: "About",
        data() {
            return {
                baseUrl: "../",
                about: json
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/sass/style";

    #about {
        width: 100%;
        min-height: 100vh;
        position: relative;
        display: flex;
        flex-direction: row;
        align-items: center;
        @include breakpoint-down($lg-max) {
            min-height: 568px;
        }
    }
    .bg {
        &__img {
            transform: scale(-1, 1);
        }
        &__show {

        }
    }
    .container {
        height: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        @include breakpoint-down($md-max) {
            flex-direction: column;
            justify-content: center;
        }
    }
    .content {
        flex: 1;
        margin-right: 50px;
        @include breakpoint-down($md-max) {
            margin-right: 0;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
    .title {
        text-transform: uppercase;
        margin-bottom: $gutter-sm;
        @include breakpoint-down($lg-max) {
            font-size: 1.5rem;
        }
        @include breakpoint-down($md-max) {
            font-size: 1.25rem;
            text-align: center;
        }
    }
    .desc {
        font-size: .75rem;
        margin-bottom: $gutter-md;
        line-height: 1.5;
        @include breakpoint-down($md-max) {
            text-align: center;
        }
    }
    .image {
        width: 50%;
        object-fit: contain;
        height: auto;
        transform: scale(-1, 1);
        @include breakpoint-down($md-max) {
            display: none;
        }
    }
    .btn {
        border-radius: 5px;
        background: $active-color;
        padding-left: $gutter-md;
        padding-right: $gutter-md;
        color: $main-color;
        text-transform: uppercase;
    }
</style>
