<template lang="pug">
    #hero
        header.header(ref="header")
            .container
                router-link.logo.animated.fadeInRight(tag="a" :to="header.homeLink")
                    img.logo__image(:src="baseUrl + header.logo")
                    h6.logo__name {{ header.name }}
                nav.nav.animated.fadeInLeft
                    router-link.nav__link(tag="a" v-for="nav in header.nav" :to="nav.link" @click.native="showDrop = !showDrop") {{ nav.name }}
                ul.drop-btn.animated.fadeInLeft(@click="extendDrop")
                    - for(i = 0; i < 2; i++)
                        li.drop-btn__bar
        transition(name="dropMenu" enter-active-class="animated slideInDown" leave-active-class="animated slideOutUp")
            .drop(v-if="showDrop")
                nav.drop-nav
                    router-link.drop-nav__link(tag="a" v-for="nav in header.nav" :to="nav.link") {{ nav.name }}
                ul.socials
                    router-link.socials__link(tag="li" v-for="social in header.socials" :to="social.link")
                        i.socials__icon( :class="social.icon")
</template>

<script>
import json from "@/store/header"

    export default {
        name: "Header",
        data() {
            return {
                baseUrl: "../",
                header: json,
                showDrop: false
            }
        },
        methods: {
            extendDrop() {
                let $header = this.$refs.header;
                this.showDrop = !this.showDrop;
                $header.classList.toggle("extended");
            }
        },
        watch: {
            '$route' (to, from) {
                if (this.showDrop) {
                    this.extendDrop();
                }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../assets/sass/style";

    #hero {
        width: 100%;
        position: fixed;
        top: 0;
        background: $main-color;
        z-index: 98;
    }
    .router-link-exact-active {
        color: $active-color;
    }
    .header {
        box-shadow: 0 0 5px $dark-25;
        position: relative;
        z-index: 2;
        background: $main-color;
        transition: all .25s ease-in-out;
        &.extended {
            box-shadow: 0 0 15px $dark-5;
        }
    }
    .container {
        height: 50px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        @include breakpoint-down($md-max) {
            height: auto;
            padding-top: $gutter-sm;
            padding-bottom: $gutter-sm;
        }
    }
    .logo {
        display: flex;
        flex-direction: row;
        align-items: center;
        text-decoration: none;
        &__image {
            height: 24px;
            margin-right: $gutter-sm / 2;
        }
        &__name {
            white-space: nowrap;
            text-transform: uppercase;
        }
    }
    .nav {
        @include breakpoint-down($md-max) {
            display: none;
        }
        &__link {
            text-decoration: none;
            font-size: .75rem;
            margin-left: $gutter-md;
            transition: all .25s ease-in-out;
            &:hover {
                color: $active-color;
            }
        }
    }
    .drop-nav {
        flex-direction: column;
        align-items: center;
        margin-bottom: $gutter-sm;
        @include breakpoint-down($md-max) {
            display: flex;
        }
        &__link {
            text-decoration: none;
            font-size: 1.25rem;
            line-height: 2;
            text-align: center;
            transition: all .25s ease-in-out;
            &:hover {
                color: $active-color;
            }
        }
    }
    .drop-btn {
        list-style: none;
        display: none;
        flex-direction: column;
        justify-content: space-around;
        align-items: flex-end;
        height: 20px;
        width: 30px;
        @include breakpoint-down($md-max) {
            display: flex;
        }
        &__bar {
            width: 100%;
            height: 2px;
            background: $second-main-color;
            &:first-child {
                width: 80%;
            }
        }
    }
    .drop {
        z-index: 1;
        padding: $gutter-sm;
        position: fixed;
        top: 50px;
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        background: $main-color;
        box-shadow: 0 0 5px $dark-25;
    }
    .socials {
        display: flex;
        flex-direction: row;
        list-style: none;
        &__link {
            cursor: pointer;
            text-decoration: none;
            margin-left: $gutter-md;
            transition: all .25s ease-in-out;
            @include breakpoint-down($md-max) {
                margin: 0 $gutter-sm;
            }
            &:hover {
                color: $active-color;
            }
        }
    }
</style>
