<template lang="pug">
    footer#footer
        .container
            router-link.logo(tag="a" :to="footer.homeLink")
                img.logo__image(:src="baseUrl + footer.logo")
                h6.logo__name {{ footer.name }}
            p.rights {{ footer.rights }}
            ul.socials
                router-link.socials__link(tag="li" v-for="social in footer.socials" :to="social.link")
                    i.socials__icon( :class="social.icon")
</template>

<script>
import json from "@/store/footer"

    export default {
        name: "Footer",
        data() {
            return {
                baseUrl: "../",
                footer: json
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../assets/sass/style";

    #footer {
        width: 100%;
        background: $main-color;
        z-index: 98;
        box-shadow: 0 0 5px $dark-25;
    }
    .container {
        height: 50px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        @include breakpoint-down($md-max) {
            flex-direction: column;
            height: auto;
            padding-top: $gutter-sm;
            padding-bottom: $gutter-sm;
        }
    }
    .logo {
        display: flex;
        flex-direction: row;
        align-items: center;
        text-decoration: none;
        @include breakpoint-down($md-max) {
            margin-bottom: $gutter-sm / 2;
        }
        &__image {
            height: 24px;
            margin-right: $gutter-sm / 2;
        }
        &__name {
            white-space: nowrap;
            text-transform: uppercase;
        }
    }
    .rights {
        font-size: .75rem;
        @include breakpoint-down($md-max) {
            margin-bottom: $gutter-sm / 2;
        }
    }
    .socials {
        display: flex;
        flex-direction: row;
        list-style: none;
        &__link {
            cursor: pointer;
            text-decoration: none;
            margin-left: $gutter-md;
            transition: all .25s ease-in-out;
            @include breakpoint-down($md-max) {
                margin: 0 $gutter-sm;
            }
            &:hover {
                color: $active-color;
            }
        }
    }
</style>
