                    h4.plans__price {{ plan.price }}
                    ul.plans__orders
                        li.plans__order(v-for="order in plan.orders" :class="{'included': order.bool}")
                            i.plans__icon.fas.fa-check(v-if="order.bool")
                            i.plans__icon.fas.fa-times(v-else-if="order.bool === false")
                            p.plans__text.hint-top-middle-s-small.hint-anim(:data-hint="order.name") {{ order.name }}
                    router-link.plans__btn.btn(tag="button" :to="{ path: '/order', query: { page: 'web', key: plan.key, name: plan.name } }") Оформить пакет
</template>

