<style lang="scss" scoped>
    @import "../../assets/sass/style";

    #plans {
        width: 100%;
        min-height: 100vh;
        position: relative;
        padding-top: 100px;
        @include breakpoint-down($lg-max) {
            min-height: 568px;
        }
    }
    [data-hint]:after {
        font-size: .75rem;
    }
    .container {
        position: relative;
    }
    .title {
        text-transform: uppercase;
        text-align: center;
        margin-bottom: $gutter-md;
    }
    .plans {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap;
        list-style: none;
        @include breakpoint-down($lg-max) {
            flex-direction: column;
        }
        &__plan {
            width: calc(100% / 3);
            padding: $gutter-md;
            background: $main-color;
            transition: all .25s ease-in-out;
            border: solid 1px $dark-25;
            @include breakpoint-down($lg-max) {
                width: 50%;
                margin-bottom: $gutter-md;
            }
            @include breakpoint-down($md-max) {
                width: 100%;
                display: flex;
                flex-direction: column;
            }
            &:hover {
                z-index: 1;
                border: solid 1px $active-color;
            }
        }
        &__name {
            margin-bottom: $gutter-sm / 2;
        }
        &__price {
            margin-bottom: $gutter-md;
            color: $active-color;
        }
        &__orders {
            padding: $gutter-sm 0;
            border-top: solid 1px $grey-25;
            border-bottom: solid 1px $grey-25;
            list-style: none;
            margin-bottom: $gutter-md;
        }
        &__order {
            display: flex;
            flex-direction: row;
            align-items: center;
            margin-bottom: $gutter-sm / 2;
            font-size: .75rem;
            &:last-child {
                margin-bottom: 0;
            }
        }
        .included {
            * {
                color: $second-main-color;
            }
        }
        &__icon {
            text-align: center;
            width: 12px;
            height: 12px;
            color: $dark-5;
            margin-right: $gutter-sm / 2;
        }
        &__text {
            position: relative;
            font-weight: bold;
            color: $dark-5;
        }
        &__btn {
            border-radius: 20px;
            border: solid 2px $active-color;
            color: $active-color;
            text-transform: uppercase;
            font-weight: bold;
            padding-left: $gutter-md;
            padding-right: $gutter-md;
            @include breakpoint-down($md-max) {
                align-self: center;
            }
        }
    }

</style>
