<template lang="pug">
    section#contacts
        .bg
            img.bg__img.cover-img.wow.fadeIn.faster(:src="baseUrl + contacts.bg")
            .bg__show.wow.slow.slideOutLeft
        .container
            .contacts
                h2.title.wow.fadeInUp(data-wow-delay="1s") {{ contacts.title }}
                ul.contacts__content.wow.fadeInUp(v-for="contact in contacts.contacts" :key="contact.key" data-wow-delay="1.25s")
                    li.contacts__main {{ contact.main }}
                    li.contacts__sub {{ contact.sub }}
                router-link.btn.wow.zoomIn(tag="button" :to="{ path: '/order', query: { page: 'consultation', key: 'yes', name: 'Нужна' } }" data-wow-delay="1.5s") {{ contacts.btnTitle }}
            swiper.comments.wow.zoomIn(:options="sliderOptions" data-wow-delay="1.25s")
                swiper-slide.comments__comment(v-for="comment in contacts.comments" :key="comment.key")
                    h4.comments__author {{ comment.author }}
                    h6.comments__date {{ comment.date }}
                    p.comments__text {{ comment.text }}
</template>

<script>
    import json from "@/store/contacts"

    export default {
        name: "Contacts",
        data() {
            return {
               baseUrl: "../",
               contacts: json,
               sliderOptions: {
                   speed: 1000,
                   autoplay: {
                       delay: 5000,
                       disableOnInteraction: false
                   },
                   direction: "vertical",
                   effect: "slide",
                   loop: true
               }
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../assets/sass/style";

    #contacts {
        width: 100%;
        position: relative;
    }
    .container {
         position: relative;
         display: flex;
         flex-direction: row;
         align-items: center;
         flex-wrap: wrap;
    }
    .contacts {
        flex: 1;
        @include breakpoint-down($md-max) {
            flex: none;
            width: 100%;
            margin-bottom: $gutter-md;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
        &__content {
            list-style: none;
            margin-bottom: $gutter-sm;
            @include breakpoint-down($md-max) {
                display: flex;
                flex-direction: column;
                align-items: center;
            }
            &:last-child {
                margin-bottom: 0;
            }
        }
        &__main {
            margin-bottom: $gutter-sm / 2;
            font-weight: bold;
        }
    }
    .title {
        text-transform: uppercase;
        margin-bottom: $gutter-sm;
        @include breakpoint-down($lg-max) {
            font-size: 1.5rem;
        }
        @include breakpoint-down($md-max) {
            font-size: 1.25rem;
            text-align: center;
        }
    }
    .btn {
        margin-top: $gutter-sm;
        border-radius: 5px;
        background: $active-color;
        padding-left: $gutter-md;
        padding-right: $gutter-md;
        color: $main-color;
        text-transform: uppercase;
    }
    .comments {
        width: 50%;
        height: 300px;
        @include breakpoint-down($md-max) {
            width: 100%;
        }
        &__comment {
            padding: $gutter-md;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: 300px;
        }
        &__author {
            text-align: center;
            margin-bottom: $gutter-sm;
            font-weight: normal;
        }
        &__date {
            margin-bottom: $gutter-sm;
        }
        &__text {
            font-size: .75rem;
            text-align: center;
        }
    }
</style>
